import Utils from '../../Utils'
import { motion } from "framer-motion"
import customTransition from '../../styles/customTransition'
import customStyles from '../../styles/customStyles'
import { LazyLoadImage } from 'react-lazy-load-image-component'
import { useState } from 'react'
import { Button } from '@mui/material'
import { useTranslation } from 'react-i18next'
const Home = () => {
    const {t} = useTranslation()
    const [media,setMedia] = useState('');
    const handleViewDemo = () =>{
        const url = Utils.getViewDemoUrl(window.categoryData,'TVC')
        console.log(url)
        setMedia(url)
    }
    console.log(media)
    return (
            <motion.div initial="hidden"
            animate="visible"
            variants={customTransition.variant} 
            >
                <LazyLoadImage
                            effect="blur"
                            placeholderSrc={`${Utils.getImageUrl('Contact.jpg')}`}
                            opacity={1}
                            src={`${Utils.getImageUrl('editor.jpg')}`}
                            alt="" 
                            style={
                            customStyles().backgroundMenu
                            }
                        />
                    {
                        media.length<=0
                        ?  
                        <Button
                            sx={{...customStyles().divCenterHome,zIndex:1}}
                            onClick={handleViewDemo}
                        >
                            {t("Home.View")}
                        </Button>
                        :   
                        <div  
                            style={{
                                ...customStyles().divCenterIframe
                            }}
                            onClick={()=>{
                                setMedia("")
                            }}
                        >
                           <iframe style={{...customStyles().divIframe}} src="https://www.youtube.com/embed/J7eYhM6wXPo" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                        </div>
                        
                    }      
               
            </motion.div>
    );
}
 
export default Home;